import Link from "next/link"
import { FC, useState } from "react"

type Props = {
  active: string
  setUrl: (tab:string) => void
}

const Header:FC<Props> = ({active, setUrl}) => {
 
  return (
    <div className={`w-full flex justify-between !bg-black/10 ${active == 'home' || active == '' ? 'absolute z-10' : ''} !p-8`}>
      <div className="">
        <img src="/img/logo-inverse.svg" className="w-36" alt="" />
      </div>
      <div className="flex gap-4 items-center ">
        <span onClick={() => setUrl("home")} className={`${active == 'home' || active == '' ? 'text-white' : 'text-gray-500'} cursor-pointer`}>INICIO</span>
        <span onClick={() => setUrl("products")} className={`${active == 'products' ? 'text-white' : 'text-gray-500'} cursor-pointer`}>PRODUCTOS</span>
        <span onClick={() => setUrl("aboutus")} className={`${active == 'aboutus' ? 'text-white' : 'text-gray-500'} cursor-pointer`}>SOBRE NOSOTROS</span>
      </div>
      <Link href="/login">REGISTRARME</Link>
    </div>
  )
}
export default Header