import ButtonDota from "@/components/utils/cards/ButtonDota";
import Image from "../../../../public/img/bg-home.png";
import { useRouter } from "next/router";
const Home = () => {
  const router = useRouter()
  return (
    <div
      className="h-full absolute top-0 flex flex-col items-center"
      style={{
        backgroundImage: `url("/img/bg-home.png")`,
        backgroundSize: "cover",
        width: "100%",
        height: "100vh",
      }}
    >
      <div className="flex flex-col h-full justify-center items-center gap-4">
      <img src="/img/logo-inverse.svg" className="w-72" alt="" />
      <span className="text-5xl uppercase font-bold">Descubre tu potencial</span>
      <div className="flex justify-center gap-4 mt-10">
        <ButtonDota onClick={() => router.push('/login')} rounded="lg" classNames="bg-white text-black font-bold text-xl">UNIRME A DOTA</ButtonDota>
        <ButtonDota onClick={() => router.push('/login')} rounded="lg" classNames="text-xl">INGRESAR A MI CUENTA</ButtonDota>
      </div>
      </div>
    </div>
  );
};

export default Home;
